body{
  background: #e0d6f1;
  font-family: 'Spectral', serif;
  font-size:28px;
}

a.inside{
  text-decoration: none;
  color:black;letter-spacing: 12px; 
  word-break: break-word;
}

a.outside{
  color:black;
  word-break: break-word;
}
.container{
  /*background:#ffabd1;*/
  position: relative;
  font-size: 24px
}
.button_dot{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 70px;
  height: 70px;
  background: #d61d2a;
  border-radius: 35px;
  border:1px solid black;
}
.sexGamePopup_container{
  background:white;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 34%;
  border: 1px solid rgba(0,0,0,1);
  padding:21px;
  -webkit-transition: opacity 1.2s ease-in-out;
  transition: opacity 1.2s ease-in-out;
}

.sexGamePopup_container2{
  background:white;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 34%;
  border: 1px solid rgba(0,0,0,1);
  padding:21px;
  opacity: 0;
  -webkit-transition: opacity .7s ease-in-out;
  transition: opacity .7s ease-in-out;
}
@-webkit-keyframes fadeInFromNone {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        display: block;
        opacity: 0;
    }

    100% {
        display: block;
        opacity: 1;
    }
}
@keyframes fadeInFromNone {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        display: block;
        opacity: 0;
    }

    100% {
        display: block;
        opacity: 1;
    }
}

.sexGamePopup{
  margin-bottom: 24px;
}
.sexGamePopup2{
  text-align: center;
  margin-bottom: 24px;
}

.sexGamePopup_loading{
  margin-bottom: 24px;
  width: 120px;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.sexGamePopup_yes{
  display: inline-block;
  border-top:1px dotted #666666;
  width: 42%;
  margin-right:5%;
}

.sexGamePopup_yes:hover{
  
